import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { WatchlistDataTableFiscalPeriodTooltip } from "../../WatchlistDataTableCells/WatchlsitDataTableFiscalPeriodHeaderCell"
import { genericColumn } from "../../genericColumn"

function fiscalYearAndPeriod(_tableName: string) {
  const title = "Fiscal Period"
  return genericColumn({
    endpoint: "companyBalanceStatement",
    columnKeys: ["fiscalYear", "period"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The company's fiscal period.",
    cell: info => {
      const { date, filingDate, fiscalYear, period } =
        info.row.original.financialData.companyBalanceStatement?.data || {}
      return (
        <WatchlistDataTableTextCell
          tooltip={<WatchlistDataTableFiscalPeriodTooltip date={date} filingDate={filingDate} />}
          value={info.getValue()}
          queryResult={info.row.original.financialData.companyBalanceStatement}
        >
          {fiscalYear}
          {period}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { fiscalYearAndPeriod }
